.TopTriangle {
    z-index: -1;
    position: absolute;
    height: 100vw;
    width: 100vw;
    max-width: 100%;
    background-image: linear-gradient(315deg, white 50%, hsla(359.31, 77.68%, 43.92%, 1) 50%);
}

.BottomTriangle {
    z-index: -1;
    position: relative;
    margin-top: -100vw;
    bottom: 0;
    height: 100vw;
    width: 100vw;
    max-width: 100%;
    background-image: linear-gradient(135deg, white 50%, hsla(359.31, 77.68%, 43.92%, 1) 50%);
}